@import url("https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@300;700&display=swap");
body,
html {
  margin: 0px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  font-family: "Roboto";
  font-style: normal;
  height: 100vh;
  width: 100vw;
}

#container {
  width: 1440px;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 96px;
}

img {
  width: 505px;
  height: 330px;
  margin-right: 95px;
}

main {
  width: 380px;
  height: 250px;
}

h1 {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}

p {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}

button {
  align-items: center;
  justify-content: center;
  display: flex;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  background: #E4C1AE;
  border-radius: 32px;
  border: none;
}

#button-text {
  font-weight: 700;
  font-size: 16px;
  color: #FFFFFF;
}

.alert {
  top: 0;
  bottom: 0;
  width: 100vw;
  position: absolute;
  background: rgba(18, 18, 18, 0.5);
  align-items: center;
  justify-content: center;
  display: none;
}

svg {
  position: relative;
  left: 868px;
  top: 40px;
}

#box {
  width: 920px;
  height: 210px;
  background: white;
}

#box h1 {
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 50px - 24px);
}

#box p {
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 24px + 37px);
}

@media screen and (max-width: 1050px) {
  #container {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  img {
    margin: 0px;
    width: 90vw;
  }
  main {
    display: flex;
    flex-direction: column;
    width: 90vw;
  }
  h1 {
    font-size: 3em;
  }
  p {
    font-size: 2em;
  }
  button {
    width: 12em;
    height: 4em;
  }
  #box {
    width: 80vw;
  }
  svg {
    top: 40px;
    left: calc(100% - 52px);
  }
  #box h1 {
    font-size: 2em;
    left: 20%;
  }
  #box p {
    font-size: 1em;
    left: 20%;
  }
}